import { Toolbar, Link as LinkM } from '@mui/material';
import { Link as LinkR } from 'react-router-dom';
import React from 'react';

const sections = [
    { id: 1, title: '', url: '#' },
    { id: 2, title: '', url: '#' },
    { id: 3, title: ' ', url: '#' },
    { id: 4, title: '', url: '#' },
];

const Navbar = () => {
    return (
        <Toolbar
            component="nav"
            variant="dense"
            sx={{ justifyContent: 'space-between', overflowX: 'auto' }}
        >
            {sections.map((section) => (
                <LinkM
                    component={LinkR}
                    color="inherit"
                    noWrap
                    key={section.id}
                    variant="body2"
                    to={section.url}
                    sx={{ p: 1, flexShrink: 0, textDecoration: 'none' }}
                >
                    {section.title}
                </LinkM>
            ))}
        </Toolbar>
    );
};

export default Navbar;
